import React from 'react';
import { config, FastTradeQSParser } from "./Utils";

import NoMatch from "./NoMatch";

function zerodhaPayload({ action, tradingsymbol, exchange, order_type, quantity, price, validity, dis_qty, variety }) {

    order_type = order_type == 'M' ? "MARKET" : order_type;
    order_type = order_type == 'L' ? "LIMIT" : order_type;

    variety = variety == 'S' ? "regular" : variety;
    validity = validity == 'D' ? "DAY" : "IOC";
    let type = action ? "BUY" : "SELL";

    return {
        "variety": variety,
        "tradingsymbol": decodeURI(tradingsymbol),
        "exchange": exchange,
        "transaction_type": type,
        "order_type": order_type,
        "quantity": parseInt(quantity),
        "readonly": false,
        "price": price ? parseFloat(price) : 0,
        "disclosed_quantity": dis_qty ? parseInt(dis_qty) : 0,
        "validity": validity
    }
}

export default function Zerodha(props) {

    let isValid = false;
    let order = [];

    let result = FastTradeQSParser();
    if (typeof result == "object") {
        isValid = true;
        result.forEach((tradeObj) => {
            const ZerodhaObj = zerodhaPayload(tradeObj);
            console.log(tradeObj, ZerodhaObj)
            order.push(ZerodhaObj);
        })
    }
    let payload = JSON.stringify(order);
    return (
        <>
            {isValid &&
                <>
                    <NoMatch showHome={false} subtitle="Please wait while redirecting to Zerodha ..." />
                    <form method="post" id="basket-form" action="https://kite.zerodha.com/connect/basket">
                        <input type="hidden" name="api_key" value={config.zeroDhaApiKey} />
                        <input type="hidden" id="basket" name="data" value={payload} />
                    </form>
                    {
                        setTimeout(() => {
                            document.getElementById("basket-form").submit()
                        }, 1000)
                    }
                </>}
            {!isValid &&
                <NoMatch title="Invalid Order" subtitle="Timeout üïê" />
            }
        </>
    )
}