export function getMinsDiff(inputDate) {
    const diffMins = (new Date()) - new Date(inputDate);
    let NoOfMins = 0;
    if (diffMins > 60e3) {
        NoOfMins = Math.floor(diffMins / 60e3);
    }
    return NoOfMins
}

export function GetCurrentMonth() {
    var month = new Array();
    month[0] = "JAN";
    month[1] = "FEB";
    month[2] = "MAR";
    month[3] = "APR";
    month[4] = "MAY";
    month[5] = "JUN";
    month[6] = "JUL";
    month[7] = "AUG";
    month[8] = "SEP";
    month[9] = "OCT";
    month[10] = "NOV";
    month[11] = "DEC";
    var today = new Date();
    let currMonth = month[today.getMonth()];
    return currMonth;
}
export function debounce(func, wait, immediate) {
    // 'private' variable for instance
    // The returned function will be able to reference this due to closure.
    // Each call to the returned function will share this common timer.
    var timeout;

    // Calling debounce returns a new anonymous function
    return function () {
        // reference the context and args for the setTimeout function
        var context = this,
            args = arguments;

        // Should the function be called now? If immediate is true
        //   and not already in a timeout then the answer is: Yes
        var callNow = immediate && !timeout;

        // This is the basic debounce behaviour where you can call this 
        //   function several times, but it will only execute once 
        //   [before or after imposing a delay]. 
        //   Each time the returned function is called, the timer starts over.
        clearTimeout(timeout);

        // Set the new timeout
        timeout = setTimeout(function () {

            // Inside the timeout function, clear the timeout variable
            // which will let the next execution run when in 'immediate' mode
            timeout = null;

            // Check if the function already ran with the immediate flag
            if (!immediate) {
                // Call the original function with apply
                // apply lets you define the 'this' object as well as the arguments 
                //    (both captured before setTimeout)
                func.apply(context, args);
            }
        }, wait);

        // Immediate mode and no wait timer? Execute the function..
        if (callNow) func.apply(context, args);
    }
}

export function FastTradeQSParser(keys, values) {
    let query = new URLSearchParams(useLocation().search);
    let isValid = false;
    let order = [];

    // Validate required params are exisit in qs.
    if (query.has("keys")
        && query.has("values")
        && query.has("time")) {
        let time = query.get("time");

        // Validate the expire time.
        if (getMinsDiff(time) < Number(config.Timeout)) {

            let Keys = query.get("keys").split("|");
            let Values = query.get("values").split("||");

            Values.forEach((value) => {
                let tradeObj = {}
                let trade = value.split("|");
                trade.forEach((val, indx) => {
                    tradeObj[Keys[indx]] = val;
                })
                order.push(tradeObj);
            });
            return order;
        }
    }
    return isValid;
}

