import React from "react";
import { getMinsDiff, FastTradeQSParser, config } from "./Utils";
import NoMatch from "./NoMatch";
import {
    useLocation
} from "react-router-dom";

function UpstoxPayload(orderItem) {
    let upstoxItem = []
    config.upStoxParams.forEach((param, indx) => {
        const key = config.tradeParams[indx];
        let val = orderItem[key] || "-";
        console.log(param, key, val);

        if (config.Mapping["upstox"][param]) {
            if (typeof config.Mapping["upstox"][param] == "string") {
                val = config.Mapping["upstox"][param];
                console.log("String", param, key, orderItem[key], val);
            } else if (config.Mapping["upstox"][param][val]) {
                val = config.Mapping["upstox"][param][val];
                console.log("Object", param, key, orderItem[key], val);

            }
        }
        upstoxItem.push(val);
    });
    return upstoxItem.join("|");
}

export default function Upstox() {
    let isValid = false;
    let order = [];
    let result = FastTradeQSParser();
    if (typeof result == "object") {
        isValid = true;
        result.forEach((tradeObj) => {
            const upstoxObj = UpstoxPayload(tradeObj);
            // console.log(tradeObj, upstoxObj)
            order.push(upstoxObj);
        })
    }

    console.log(order.join("||"));

    return (
        <>
            {isValid &&
                <>
                    <NoMatch showHome={false} subtitle="Please wait while redirecting to Upstox..." />
                    {
                        // "https://pro2.upstox.com/trade/?orders=NSE|FO|-|BANKNIFTY20JUNFUT|20|-|buy|co|i|day|m|18209|-|-|-|-|-||NSE|FO|-|INFY20JUNFUT|1200|-|sell|simple|d|day|m|-|-|-|-|-|-"

                        setTimeout(() => {
                            window.location.href = `https://pro2.upstox.com/trade/?orders=${order.join("||")}`;

                            console.log("Done");
                        }, 1000)
                    }
                </>}
            {!isValid &&
                <NoMatch title="Invalid Order" subtitle="Timeout üïê" />
            }
        </>
    )
}